@import "/src/scss/abstracts/_variable.scss";

$margin : "20px 50px";

.profilecontainer {
    width: 100%;
    max-width: 1920px;
    padding-top: 50x;


    &__title {
        border-radius: 10px;
        background-color: $btwColor5;
        padding: 15px;
        padding-left: 35px;
        margin: $margin;

        .greet {
            font-family: $font-title;
            color: $btwColor2;
            font-size: 22px;
            font-weight: bold;
        }

        .intro {
            font-family: $font-family;
            color: $btwColor2;
            font-size: 14px;
            font-weight: 100;
            padding-top: 5px;
            margin-bottom: 10px;
        }

        .imagebtn {
            vertical-align: middle;
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 10px;
            margin-top: 20px;

            cursor: pointer;

            img {
                border-radius: 50%;
                height: 120px;
                width: 120px;
                border-color: $btwColor1;

                object-fit: cover;
            }

            &__btn {
                position: relative;
                right: 0;
                top: calc(45% - 20px);
                padding: 5px 20px 10px 20px;
                margin-top: 52px;
                margin-left: 10px;

                background-color: $btwColor2;
                border: 1px solid $btwColor1;
                border-radius: 10px;
                color: $btwColor1;
                font-family: $font-title;
                font-size: 15px;
                font-weight: 100;

                height: 35px;

                cursor: pointer;
                &--hide{
                    display: none;
                }

                &:hover {
                    transform: scale(1.05);
                    background-color: $btwColor2;
                }
            }
        }
    }

    &__infor {
        padding-left: 20px;
        margin-top: 25px;
        background-color: #252425;
        padding: 15px;
        padding: 10px 50px;
        border-radius: 10px;
        margin: $margin;

        &__title {
            font-family: $font-title;
            color: $btwColor6;
            font-size: 25px;
            font-weight: 400;
            margin-top: 10px;
            padding: 0;

        }
&__detail{
    padding: 0 20px;
    display: grid;
    grid-template-areas:
    'name name birth'
    'email email gender'
    'city district ward';
    gap: 20px;
}
