@import "../scss/abstracts/_variable.scss";

.dialogchangepass__title{
    font-family: $font-title;
    font-size: $sectionTitle-text;
    color: $btwColor6;

    text-align: center;

}
.dialogchangepass__btn{
    font-family: $font-title;
    font-size: $subsub-title-text;
    font-weight: 100;
    text-align: center;
    justify-content: center;
    text-transform: capitalize;

    width: 110px;

    border-radius: 10px;
    border: 0px;

    margin-right: 20px;
    padding: 5px 10px;

    &:hover {
        transform: scale(1.05);
    }

    &--contained {
        background-color: $btwColor1;
        color: $btwColor2;

        &:hover {
            background-color: $btwColor1;
        }
    }

    &--outlined {
        background-color: $btwColor2;
        color: $btwColor1;
        border: 1px solid $btwColor1;

        &:hover {
            background-color: $btwColor2;
        }
    }
}