@import "/src/scss/abstracts/_variable.scss";

.moviedetailallcontainer{
    width: 100%;

    &__alerts{
        position: fixed;
        top: 0px;
        right: 0px;
        z-index:9999; 

        margin: 20px ;
        background-color: transparent;
    }

    &__banner{
        width: calc( 100% - 150px);
        height: 65vh;
        margin-bottom: 20px;
        z-index: 1;
        .banner{
            width: 100%;
            height: 100%;
            position: relative;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            Button {
                font-family: $font-title;
                font-size: $subsub-title-text;
                font-weight: 100;
                text-align: center;
                justify-content: center;
                width: 130px;

                background-color: $btwColor1;
                color: $btwColor2;
                border-radius: 10px;
                border-color: $btwColor1;
                opacity: 0;

                padding: 3px 15px;
                
                z-index: 4;
                position: absolute;
                top: calc(50% - 10px);
                left: calc(50% - 70px);
                &:hover {
                    transform: scale(1.05);
                }
            }

            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                width: 100%;
                height: 100%;
                transform: scale(1.03);
                background: rgba(black, 0.6);
                z-index: 2;
                transition: 0.5s;
                opacity: 0;
            }

            &:hover {
                &::before {
                    opacity: 1;
                }

                Button {
                    opacity: 1;
                    transform: scale(1.05);
                }
                img{
                    transform: scale(1.03);
                }

            }
        } 
    }
    .moviedetailcontainer {
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
    
        &__img {
            padding-top: 10px;
            width: 25%;
            z-index: 1;
            margin-right: 50px;
            .img {
                position: relative;
                img {
                    width: 100%;
                    aspect-ratio: 3/5;
                }
                Button {
                    font-family: $font-title;
                    font-size: $subsub-title-text;
                    font-weight: 100;
                    text-align: center;
                    justify-content: center;
                    width: 130px;
    
                    background-color: $btwColor1;
                    color: $btwColor2;
                    border-radius: 10px;
                    border-color: $btwColor1;
                    opacity: 0;

                    padding: 3px 15px;
                    
                    z-index: 4;
                    position: absolute;
                    top: calc(50% - 10px);
                    left: calc(50% - 70px);
                    &:hover {
                        transform: scale(1.05);
                    }
                }
    
                &:before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    width: 100%;
                    height: 100%;
                    transform: scale(1.05);
                    background: rgba(black, 0.6);
                    z-index: 2;
                    transition: 0.5s;
                    opacity: 0;
                }
    
                &:hover {
                    &::before {
                        opacity: 1;
                    }
    
                    Button {
                        opacity: 1;
                        transform: scale(1.05);
                    }
                    img{
                        transform: scale(1.05);
                    }
    
                }
            }
    
        }
    
        &__gridtextfield {
            width: 60%;
            display: grid;
            height: auto;
            grid-template-columns: auto auto auto auto;
            grid-template-areas:
                'name name name name'
                'time time onair onair'
                'age age sub sub '
                'director director  manufactor manufactor'
                'type type type type'
                'actor actor actor actor'
                'trailer trailer trailer trailer'
                'sum sum sum sum';
            gap: 10px;
            row-gap: 15px;
        }
    
       
    }
}
