@font-face{font-family:"Montserrats";src:url("../../styles/fonts/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Oswald";src:url("../../styles/fonts/Oswald-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrats_Normal";src:url("../../styles/fonts/Montserrat-VariableFont_wght.ttf") format("truetype")}.cinemaList{margin:auto;width:calc(100vw - 180px);overflow-x:hidden;overflow-y:hidden;justify-items:center;align-items:center;padding-top:80px;padding-bottom:40px;min-height:100vh}.cinemaList__title{display:flex;width:100%}.cinemaList__title__title{font-family:"Oswald";font-size:35px;color:#fff;margin-right:auto;padding-bottom:10px;max-width:2000px}.cinemaList__title__addbutton{font-family:"Oswald";font-size:15px;font-weight:100;height:35px;text-align:center;justify-content:center;text-transform:capitalize;vertical-align:middle;width:100px;margin:auto;border-radius:10px;border:0px;margin-right:20px;padding:0px 5px;background-color:#feae00;color:#0d0e13}.cinemaList__title__addbutton:hover{background-color:#feae00;transform:scale(1.05)}.cinemaList__grid{display:grid;max-width:2000px;margin-left:auto;margin-top:10px;margin-right:auto;padding:10px;gap:25px;grid-template-columns:repeat(auto-fit, minmax(170px, 245px))}.cinemaList__promotion{padding-top:20px;position:relative}.cinemaList__promotion__title{font-family:"Oswald";font-size:25px;z-index:1}.cinemaList__promotion__titlespan{height:2px;width:80px;background-color:#feae00;position:absolute;z-index:1;left:0}.cinemaList__promotion .promotionList{justify-content:start;align-items:start;margin-top:15px}.cinemaAdd__btn{font-family:"Oswald";font-size:17px;font-weight:100;text-align:center;justify-content:center;text-transform:capitalize;width:130px;border-radius:10px;border:0px;margin-right:20px;padding:5px 10px}.cinemaAdd__btn:hover{transform:scale(1.05)}.cinemaAdd__btn--contained{background-color:#feae00;color:#0d0e13}.cinemaAdd__btn--contained:hover{background-color:#feae00}.cinemaAdd__btn--outlined{background-color:#0d0e13;color:#feae00;border:1px solid #feae00}.cinemaAdd__btn--outlined:hover{background-color:#0d0e13}.cinemaAdd{align-items:center;justify-content:center;text-align:center;scrollbar-color:#221f1f #221f1f}.cinemaAdd__title{font-family:"Oswald";font-size:25px;color:#fff;text-align:left;margin-bottom:10px}.cinemaAdd__detail{padding:10px}.cinemaAdd__detail__field__date ::-webkit-calendar-picker-indicator{filter:invert(1)}.cinemaAdd__detail__imagebtncontainer{justify-content:center;text-align:center;position:relative;z-index:1;margin-top:10px}.cinemaAdd__detail__imagebtncontainer::before{position:absolute;top:0;left:0;z-index:2;height:100%;width:100%;content:"";opacity:0;background:rgba(0,0,0,.6);transition:.5s}.cinemaAdd__detail__imagebtncontainer input{background-color:transparent;border-radius:3px;color:transparent;position:absolute;top:calc(50% - 10px);left:calc(50% - 60px);z-index:3;opacity:0;padding:4px 16px}.cinemaAdd__detail__imagebtncontainer input::before{content:"Chọn ảnh";background-color:#feae00;border-radius:10px;color:#0d0e13;font-family:"Oswald";font-size:16px;padding:4px 16px;width:100%;height:100%;-webkit-user-select:none;cursor:pointer}.cinemaAdd__detail__imagebtncontainer input::-webkit-file-upload-button{display:none}.cinemaAdd__detail__imagebtncontainer image{aspect-ratio:3/5;height:50%;object-fit:cover;z-index:1}.cinemaAdd__detail__imagebtncontainer:hover::before,.cinemaAdd__detail__imagebtncontainer:hover input,.cinemaAdd__detail__imagebtncontainer:hover image{transform:scale(1.03)}.cinemaAdd__detail__imagebtncontainer:hover::before,.cinemaAdd__detail__imagebtncontainer:hover input{opacity:1}