@import "../../../scss/abstracts/_variable.scss";

.moviedetailcontainer {
    display: -webkit-inline-box;
    width: 100%;
    margin-bottom: 10px;

    .moviedetailcontainer__img {
        aspect-ratio: 3/4;
        width: 25%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .moviedetailcontainer__detail {
        width: 75%;
        padding-right: 7%;
        padding-left: 30px;

        h1 {
            font-family: $font-title;
            font-weight: 500;

            font-size: 25px;
            text-transform: uppercase;
            word-spacing: 1px;
        }

        .moviedetailcontainer__detail__des {
            font-family: $font-family-normal;
            font-weight: 400;
            font-size: $normal-text;
            line-height: 20px;

            margin-top: 5px;
            padding-bottom: 5px;
        }

        .p-yellow {
            font-family: $font-family-normal;
            color: $btwColor1;
            font-size: $normal-text;
            font-weight: bold;
            color: $btwColor1;
            font-weight: 400;
        }

        div {
            font-family: $font-family-normal;
            color: $btwColor6;
            font-size: $normal-text;
            font-weight: normal;
            padding-top: 10px;
            word-wrap: wrap;
        }

        .moviedetailcontainer__detail__rate {
            display: -webkit-inline-box;
            padding-top: 5px;
            padding-bottom: 5px;

            .icon {
                color: $btwColor1;
                padding-top: 10px;
            }

            .rate {
                font-family: $font-family-normal;
                font-size: $normal-text;
                color: $btwColor6;

                padding-left: 5px;
                font-weight: 100;

            }
        }

        .moviedetailcontainer__detail__btncontainer {
            display: inline;
            margin-bottom: 5px;

            .btn_book,
            .btn_trailer {
                margin-top: 40px;
                width: 135px;
                height: 36px;
                border-radius: 10px;
       
                font-size: $subsub-title-text;
                font-weight: 100;
                font-family: $font-title;
                text-transform: capitalize;
              

                &:hover {
                    transform: scale(1.05);
                }
            }

            .btn_book {
                color: $btwColor2;
                background: $btwColor1;
                a{
                    color: $btwColor2;
                }

                &:hover {
                    background: $btwColor1;
                    color: $btwColor2;
                }
            }

            .btn_trailer {
                border: 1px solid $btwColor1;
                background: $btwColor2;
                color: $btwColor1;
                margin-left: 20px;

                a{
                    &:hover{
                        color:  $btwColor1;
                    } 
                }

                &:hover {
                    background: $btwColor2;
                    border-color: $btwColor1;
                }
            }
        }
    }

}