@import "/src/scss/abstracts/_variable.scss";

.signuptab {
  width: 85%;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;

  .signuptab__birthday {
    text-align: start;
  }

  &__rows {
    width: 100%;
    vertical-align: bottom;
    display: grid;
    column-gap: 30px;

    grid-template-columns: repeat(auto-fit, minmax(240px, auto));

 
    &__birthday {

      max-width: 100%;
      width: 500px;
    }
  }

  .btn {
    margin-top: 15px;
    background: $btwColor1;
    width: 130px;
    height: 40px;
    border-radius: 7px;
    font-size: 16px;
    font-weight: 600;
    color: black;
    font-family: $font-title;

    align-self: center;

    &:hover {
      transform: scale(1.05);
      background: $btwColor1;
    }
  }

  &__subtitle {
    margin-top: 12px;
    font-size: 10px;
    font-family: $font-family;
    color: white;

    &__a {
      margin-left: 5px;
      font-weight: 800px;
      color: $btwColor1;
    }
  }
}