@import "../../../../scss/abstracts/_variable.scss";

.seatcontainer {
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2px 1px;

  &__seat {
    height: 23px;
    width: 23px;
    margin: 3px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    color: $btwColor5;

    &--selected {
      color: $btwColor1;
    }

    &--occupied {
      color: #8E8E8E;
    }

    &:not(.occupied):hover {
      cursor: pointer;
      transform: scale(1.2); 
    }

  }
  &__number{
    font-family: $font-family-normal;
    font-size: 12px;
    font-weight: 500;
  }
  &:nth-of-type(2) {
    margin-right: 18px;
  }

  &:nth-last-of-type(2) {
    margin-left: 18px;
  }
}