@import "../../../scss/abstracts/_variable.scss";

.movieinforcontainer{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    padding: 80px 120px 40px 100px;

    &__breadcrumbs{
        margin-bottom: 10px;
    }
    &__titlebtn{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: space-between;
        &__title{
            font-family: $font-title;
            font-size: $sectionTitle-text;
            font-weight: 500;
            text-transform: uppercase;
            color: $btwColor6;

            margin-bottom: 20px;
        }
        &__btn{
            &--hide{
                display: none
            }

            Button{
                background-color: $btwColor1;
                border-radius: 10px;
                border: 0px;

                color : $btwColor2;
                font-weight: 100;
                font-family: $font-title;
                font-size: $subsub-title-text;
                text-transform: capitalize;

                padding: 6px 18px;
                &:hover{
                    transform: scale(1.05);
                    background-color: $btwColor1;
                }
                &--hide{
                    display: none;
                }
            }
        }
        
    }
    
    .movieinforcontainer__tabscontainer{
        z-index: 2;
        margin-bottom: 20px;
        width: 100%;
        .movieinforcontainer__tabscontainer_tabs{
          margin-bottom: 10px;
          display: flex;
          justify-content: center;
          align-items: center ;
          text-align: center; 
          flex-wrap: wrap;
            .tab{
              font-size: $tab-text;
              font-family: $font-title;
              font-weight: normal;
              text-transform: capitalize;
              padding-right: 20px;
            }
        }
    }
    .movieinforcontainer__calendarandcomment{
        width: 100%;
    }

}