@import "../../scss/abstracts/_variable.scss";

.cinemaDetail {
    margin: auto;
    width: calc(100vw - 180px);
    max-width: 2000px;
    padding-top: 80px;
    padding-bottom: 40px;

    &__title {
        font-family: $font-title;
        font-size: $sectionTitle-text;
    }

    &__content {
        padding-top: 20px;
        font-family: $font-family-normal;
        font-size: 14px;
        line-height: 25px;
        text-align: justify;
      

        &__img {
            width: 60%;
            align-items: flex-start;
            padding-top: 30px;
        }
        &__title{
            font-weight: 600;
            font-size: 20px;
            padding-top: 20px;
            padding-bottom: 10px;
        }
    
       
    }
}