@font-face{font-family:"Montserrats";src:url("../../styles/fonts/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Oswald";src:url("../../styles/fonts/Oswald-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrats_Normal";src:url("../../styles/fonts/Montserrat-VariableFont_wght.ttf") format("truetype")}.promoList{margin:auto;justify-items:center;align-items:center;padding:80px 90px 40px 70px;min-height:100vh}.promoList__container{width:calc(100vw - 200px);overflow-x:hidden;overflow-y:hidden}.promoList__container__title{display:flex;width:100%;margin-left:15px}.promoList__container__title__title{font-family:"Oswald";font-size:35px;color:#fff;margin-right:auto;padding-bottom:10px;max-width:2000px}.promoList__container__title__addbutton{font-family:"Oswald";font-size:15px;font-weight:100;height:35px;text-align:center;justify-content:center;text-transform:capitalize;width:110px;border-radius:10px;border:0px;margin-right:20px;padding:5px 10px;background-color:#feae00;color:#0d0e13}.promoList__container__title__addbutton:hover{background-color:#feae00;transform:scale(1.05)}.promoList__container__grid{display:grid;max-width:2000px;margin-left:auto;margin-right:auto;margin-top:10px;padding-bottom:10px;padding-right:10px;gap:30px;grid-template-columns:repeat(3, minmax(200px, 400px))}.promoList__container__titlemovie{font-family:"Oswald";font-size:25px;z-index:1;margin-top:15px}.promoList__container__titlespanmovie{height:3px;width:170px;margin-left:70px;margin-bottom:20px;background-color:#feae00;position:absolute;z-index:1;left:0}.promoList__container__movieshowing{height:fit-content;margin-top:15px}.promoList__container__movieshowing .swiper{padding-top:15px;padding-bottom:15px;height:auto}.promoList__container__movieshowing .swiper__slide{height:100%;width:fit-content}.dialogaddpromotion__detail{justify-content:center;text-align:center;padding:0px 10px}.dialogaddpromotion__detail__titleimg{font-family:"Montserrats";font-size:15px;font-weight:500;color:#fff;margin-bottom:10px;text-align:left}.dialogaddpromotion__detail__imgbtn{position:relative;z-index:1;width:100%;margin-right:20px;justify-content:center;align-items:center;text-align:center}.dialogaddpromotion__detail__imgbtn::before{content:"";position:absolute;top:0;left:0;display:block;width:100%;height:100%;transform:scale(1.03);background:rgba(0,0,0,.6);z-index:2;transition:.5s;opacity:0}.dialogaddpromotion__detail__imgbtn img{width:100%;height:100%;object-fit:cover;aspect-ratio:2/1}.dialogaddpromotion__detail__imgbtn input{text-align:center;justify-content:center;width:120px;padding:5px 8px;color:transparent;opacity:0;z-index:4;position:absolute;top:calc(50% - 10px);left:calc(50% - 40px)}.dialogaddpromotion__detail__imgbtn input::before{content:"Chọn ảnh";font-family:"Oswald";font-size:16px;color:#0d0e13;background-color:#feae00;display:inline-block;border-radius:3px;padding:5px 8px;outline:none;-webkit-user-select:none;cursor:pointer}.dialogaddpromotion__detail__imgbtn input::-webkit-file-upload-button{display:none}.dialogaddpromotion__detail__imgbtn:hover::before{opacity:1}.dialogaddpromotion__detail__imgbtn:hover input{opacity:1;transform:scale(1.05)}.dialogaddpromotion__detail__imgbtn:hover img{transform:scale(1.02)}.dialogaddpromotion__title{flex:1;color:#fff;font-family:"Oswald";font-size:30px;padding-left:20px}