@import "../../scss/abstracts/_variable.scss";

$margin: 10px;

.numericcontainer {
    background-color: transparent;
    display: flex;
    flex-wrap: wrap;
    vertical-align:middle;
    justify-content: center;
    text-align: center;
    align-items: center;

    &__icon {
        background-color: $btwColor2;
        height: 18px;
        width: 18px;
        color: $btwColor1;
        margin-right: $margin;
    }

    &__number {
        font-family: $font-family-normal;
        font-size: 20px;
        font-weight: 500;
        color: $btwColor6;
        margin-right: $margin;
        
    }
}