@import "../../../../scss/abstracts/_variable.scss";

.paymentinforcontainerparent {
    .paymentinforcontainer {
        display: block;
        justify-content: center;
        align-items: center;
        width: 40vw;
        padding-bottom: 20px;
        border: 2px solid #000;
        margin-right: 50px;

        &--hide {
            display: none;
        }

        &__title {
            font-family: $font-family-normal;
            color: $btwColor1;
            font-size: 20px;
            width: 100%;
            padding: 15px;
            font-weight: 500;
            background-color: $btwColor2;
            margin-bottom: 15px;
        }

        &__textfield {
            display: flex;
            flex-wrap: wrap;
            vertical-align: middle;
            margin: 10px 20px;

            &--hiden {
                display: none;
            }

            .textfield {
                flex: auto;
                display: block;
                width: 50%;
                background-color: transparent;

            }

            .lable {
                position: relative;
                top: 50%;
                flex: 1;
                font-family: $font-family-normal;
                font-size: 18px;
                color: $btwColor5;
                width: auto;
                padding-top: 10px;
            }

            .select {
                flex: auto;
                display: block;
                width: 50%;
                color: $btwColor1;
                font-family: $font-family-normal;
                font-size: 15px;
                font-weight: 500;
                border: 1px;
                border-radius: 5px;
                border-color: $btwColor6;
                background-color: transparent;
            }


        }


    }
}