@import "../../scss/abstracts/_variable.scss";

.cinemaContainer {
    aspect-ratio: 7/10;
    min-height: fit-content;
    width: 100%;
    
    border-radius: 10px;
    padding: 0px;
    position: relative;
    &:hover {
        transform: scale(1.05);
    }
    &__closebtn {

        position: absolute;

        top: 2%;
        right: 2%;
        z-index: 4;
        border-radius: 50%;
        justify-content: center;
        background-color: transparent;

        &__icon {

            width: 40px;
            height: 40px;

            &:hover {
                background-color: #4c4c4ccf;
            }

            &__icon {
                align-self: center;
            }

        }
    }


    &__Info {
        width: 100%;
        text-align: center;
        padding: 5px;
        padding-top: 10px;
        flex: 1;
        background-color: $btwColor1;
        font-family: $font-family-normal;
        border: double;
        height: 50%;
        border-color: white;
        border-radius: 10px 10px 0px 0px;

        &__nameLogo {
            font-family: $font-title;
            font-size: $itemTitle-text;
        }

        &__nameCinema {
            font-family: $font-title;
            font-size: $itemTitle-text;
            text-transform: uppercase;
            color: black;
            font-weight: 800;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            line-clamp: 1;
            -webkit-box-orient: vertical;
        }

        &__dateContainer {
            align-items: center;
            text-align: center;
            display: block;
            margin: auto;
            margin-top: 5px;
            border-radius: 100px;
            background-color: $btwColor5;
            width: fit-content;

            p {
                padding: 5px 20px;
                color: red;
                font-size: 11px;
                align-items: center;
                font-weight: 800;
                font-family: $font-family;
            }
        }

        &__address {
            padding-top: 15px;
            font-size: 12px;
            color: black;
            font-weight: 500;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            line-clamp: 2;
            -webkit-box-orient: vertical;
        }

        &__phoneNumber {
            padding-top: 5px;
            font-size: 12px;
            color: black;
            font-family: $font-family;
            font-weight: 800;
            margin-bottom: 5px;
        }

    }

    &__imgContainer {
        width: 100%;
        height: 50%;
    overflow-y: hidden ;
    border-radius: 0px 0px 10px 10px;
        &__img {
            height: 100%;
            min-width: 100%;
            display: block;
            border-radius: 0 0 10px 10px;
        }
    }
}

.cinemadetailAdmin__btn {
    font-family: $font-title;
    font-size: 17px;
    font-weight: 100;
    text-align: center;
    justify-content: center;
    text-transform: capitalize;

    width: 130px;

    border-radius: 10px;
    border: 0px;

    margin-right: 20px;
    padding: 5px 10px;

    &:hover {
        transform: scale(1.05);
    }

    &--contained {
        background-color: $btwColor1;
        color: $btwColor2;

        &:hover {
            background-color: $btwColor1;
        }
    }

    &--outlined {
        background-color: $btwColor2;
        color: $btwColor1;
        border: 1px solid $btwColor1;

        &:hover {
            background-color: $btwColor2;
        }
    }
}

.cinemadetailAdmin__detail__field__dateBegin {
    ::-webkit-calendar-picker-indicator {
        filter: invert(1);

    }
}