@import "/src/scss/abstracts/_variable.scss";

.billdetailcontainer{
    padding: 70px 100px 40px 100px;
    width: calc(100vw);
    &__title{
        font-size: $viewTitle-text;
        font-family: $font-title;
        font-weight: 500;
    }
    &__content{
        display: grid;
        grid-template-areas:'item item cusinfor';
        gap: 0px;

        background-color: transparent;

        width:  calc(100vw - 200px);
        
        &__item{
            grid-area: "item";
            margin-right: 30px;
            .nameroomcinema,
            .namemovie{
                font-family: $font-title;
                font-weight: bold;
            }
            .namemovie{
                font-size: $viewTitle-text;
                color: $btwColor1;
            }
            .nameroomcinema{
                font-size: $sectionTitle-text;
                color: $btwColor6;
                margin-bottom: 5px;
                font-weight: 500;
            }
            .showtime{
                font-family: $font-family;
                font-weight: 500;
                font-size: $subsectionTitle-text;
                color: $btwColor6;
                margin-bottom: 20px;

            }
            .total{
                font-family: $font-family;
                font-weight: 500;
                font-size: $sectionTitle-text;
                color: $btwColor6;

                width: 100%;
                justify-content: right;
                text-align: right;

                margin: 15px 0 10px 0;
            }
            .note{
                &__title{
                    font-family: $font-title;
                    font-weight: 500;
                    font-size: $sectionTitle-text;
                    color: $btwColor1;

                    margin-bottom: 5px;
                }
                &__content{
                    font-family: $font-family-normal;
                    font-weight: 100;
                    font-size: $normal-text;
                    color: $btwColor6;
                    letter-spacing: 1px;

                    margin-bottom: 3px;
                }
            }
        }
        &__cusinfor{
            display: flex;
            flex-wrap: wrap;
            grid-area: "cusinfor";
            .verticalline{
                border-left: 3px solid $btwColor6;
                height: calc(100vh - 200px);
                width: 3px;

                padding-right: 30px;
            }
    
            .cusinfor{
               
                .title{
                    font-family: $font-title;
                    font-size: $sectionTitle-text;
                    font-weight:500;
                    color: $btwColor6;
    
                    letter-spacing: 1px;
                    margin-top: 10px;
                    margin-bottom: 10px
    
                }
                .total{
                    font-family: $font-title;
                    font-size: $viewTitle-text;
                    font-weight: bold;
                    color: $btwColor1;
    
                    margin-bottom: 10px;
                    margin-left: 10px;
                }
                .content{
                    font-family: $font-family-normal;
                    font-size: $subTitle-text;
                    font-weight: 500;
                    color: $btwColor6;
    
                    margin-bottom: 7px;
                    margin-left: 10px;
                }
            }
        }
       
    }
}