@import "/src/scss/abstracts/_variable.scss";

.movieadmincontainer {
    padding: 80px 120px 40px 100px;
    width: 100vw;
    justify-content: start;

    &__titlebtn {
        margin-bottom: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        &__title {
            .title {
                font-family: $font-title;
                font-size: $sectionTitle-text;
                font-weight: 500;
                color: $btwColor6;
            }

            hr {
                border: 1px solid $btwColor1;
                color: $btwColor1;
                width: 130px;
                background-color: $btwColor1;
            }
        }

        &__btn {
            Button {
                background-color: $btwColor1;
                border-radius: 5px;
                border-color:  $btwColor1 ;

                color: $btwColor2;
                font-family: $font-title;
                font-size: $subsub-title-text;
                font-weight: 100;
                text-transform: capitalize;

                height: 35px;

                padding: 3px 10px 5px 10px;

                &:hover {
                    transform: scale(1.05);
                    background-color: $btwColor1;
                }
            }

        }


    }

    &__grid {
        justify-content: start;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(calc((100% - 100px )/ $movie-in-row), 0));

        grid-gap: 30px;
        margin-bottom: 10px;
    }

}
.movieadmin__addoutstandingmovie__title{
    font-family: $font-title;
    font-size: $sectionTitle-text;
    font-weight: 500;
    color: $btwColor6;

    margin-bottom: 15px;
}
.movieadmin__addourstandingmovie__moviesgrid{
    margin-top: 20px;
    padding: 10px;
    justify-content: start;
    display: grid;

    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(calc((100% - 50px)/ 3), 0));
    
    max-width: calc(200vw / 3);

}