@import "../../../../scss/abstracts/_variable.scss";

.cinemacard {
    display: grid;
    grid-template-columns: 300px auto;
    // flex-wrap: wrap;
    margin-top: 20px;
    width: 80vw;

    .cinemacard__infor {
        // flex: 1;
        background-color: $btwColor2;
        border-radius: 7px;
        padding: 15px 0px 10px 15px;
        margin-right: 40px;
        display: inline-block;

        .cinemacard__infor__name,
        .cinemacard__infor__address,
        .cinemacard__infor__ggmapurl,
        .cinemacard__infor__typesubage {
            font-family: $font-family-normal;
            padding-bottom: 7px;

        }

        .cinemacard__infor__name,
        .cinemacard__infor__address {
            color: $btwColor6;
            padding-right: 10px;
        }

        .cinemacard__infor__name {
            font-size: $subsectionTitle-text;
            font-weight: 100;
            font-family: $font-title;
            word-spacing: 1px;
        }

        .cinemacard__infor__address {
            font-size: 13px;
            line-height: 18px;
            font-weight: normal;
        }

        .cinemacard__infor__ggmapurl {
            font-size: 13px;
            font-weight: normal;
            color: $btwColor1
        }

        .cinemacard__infor__typesubage {
            padding: 5px;
            margin-left: 50px;
            margin-bottom: 10px;
            margin-top: 10px;
            color: $btwColor1;
            border-radius: 5px 0px 0px 5px;
            background-color: $btwColor1;

            .typeandsub,
            .age {
                font-family: $font-family;
                color: $btwColor2;
                font-size: $normal-text;
                font-weight: bold;
                justify-content: space-between;
            }

            .age {
                color: $btwColor6;
                padding-left: 30px;
            }

        }
    }

    .cinemarcard__showtimes {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        flex-wrap: wrap;
        flex: auto;
        height: fit-content;

        .cinemarcard__showtimes__showtime {
            position: relative;
            .btn{
                position: absolute;
                top: 0;

                margin-right: 10px;
                padding: 0px;

                background-color: $btwColor5;
                border-radius: 7px;
                border: 0px;


                color: $btwColor2;
                font-family: $font-family;
                font-size: $subsub-title-text;
                font-weight: bold;

                height: 38px;
                width: 100%;

                &:focus {
                    background-color: $btwColor1;
                }
            }

            .icon {
                border-radius: 50%;
                justify-content: center;
                background-color: #4c4c4ccf;
                position: absolute;
                width: 23px;
                height: 23px;
                top: -25%;
                right: -8%;
                z-index: 4;
                Button{
                    background-color: transparent;
                    border-width:0px ;
                    margin: 5px 3px 5px 7px;

                }
                &:hover{
                    background-color: #4c4c4cde;
                    transform: scale(1.1);
                }
            }
            Link{
                .btn{
                    margin-right: 10px;

                    background-color: $btwColor5;
                    border-radius: 7px;
                    border: 0px;
    
                    color: $btwColor2;
                    font-family: $font-family-normal;
                    font-size: 16px;
                    font-weight: bold;
    
                    height: 38px;
                    width: 93%;
    
                    &:focus {
                        background-color: $btwColor1;
                    }
                }
            }
        }

        &__btn {
            margin-right: 10px;
            padding: 0px;

            font-family: $font-title;
            font-size: $subsub-title-text;
            font-weight: 300;
            text-align: center;
            text-transform: capitalize;
            justify-content: center;

            display: flex;
            flex-wrap: wrap;

            background-color: $btwColor1;
            color: $btwColor2;
            border-radius: 10px;
            border: 0px;

            height: 38px;

            &:hover {
                transform: scale(1.05);
                background-color: $btwColor1;

            }

        }
    }
}

.cinema__dialog__btn {
    margin-right: 10px;
    padding: 3px 12px;

    font-family: $font-title;
    font-size: 15px;
    font-weight: 100;
    text-align: center;
    text-transform: capitalize;
    justify-content: center;

    border-radius: 5px;
    border-color: $btwColor1;

    &:hover {
        transform: scale(1.05);
    }

    &--contained {
        background-color: $btwColor1;
        color: $btwColor2;

        &:hover {
            background-color: $btwColor1;

        }
    }

    &--outlined {
        background-color: $btwColor2;
        color: $btwColor1;
        border: 1px solid $btwColor1;

        &:hover {
            background-color: $btwColor2;
            border: 1px solid $btwColor1;
        }
    }
}

.cinema__dialog__title {
    font-family: $font-title;
    font-size: 25px;
    font-weight: 500;
    word-spacing: 1px;
    color: #fff;
    padding: 10px 10px 5px 17px;
}

.cinema__dialog__formlable {
    font-family: $font-family-normal;
    font-size: 18px;
    font-weight: 500px;
    color: $btwColor6;
}
.cinema__dialog__formfield{
    margin-left: 10px;
    margin-top: 5px
}