@import "../../../scss/abstracts/_variable.scss";

.containermoviecalendar{
    width: calc(100vw - 230px);
    height: auto;
    justify-content: center;
    padding-right: 100x;
    &__selectprovince{
        width: 200px;
        height: auto;
        margin-bottom: 20px;
    }
        .containermoviecalendar__tabscontainer{
            margin-bottom: 30px;
            width: 100%;
            justify-content: center;
            .MuiTabs-scrollButtons.Mui-disabled {
                opacity: 0.3;
              }
            .tabscontainer__calendar{
                margin-top: 20px;
              }
            .datecalendar
            .note{
                font-family: $font-family-normal;
                color: $btwColor6;
                font-weight: 600;
                padding-left: 5px;
                padding-right: 5px;
            }
            .datecalendar{
                font-size: 20px;
                font-weight: bold;
                text-transform: capitalize;
            }
            .note{
                font-weight: 500;
                font-size: 15px;
            }
    
        }
    
}


