@import '../../../scss/abstracts/_variable.scss';

.containerquickingbook{
    background: rgba(0, 0, 0, .3);
    width: 334px;
    height: 315px;
    text-align: center;
    position: absolute;
    top: 15%;
    right: 10%;
    z-index: 2;

    &__alerts{
        position: fixed;
        top: 40px;
        right: 0px;
        z-index: 9999; 

        margin: 20px ;
        background-color: transparent;
    }

    .select{
        background: rgba(196, 196, 196, .8);
        width: 285px;
        height: 38px;
        margin-bottom: 15px ;
        justify-content: space-around;
        text-align: left;
        color: $btwColor6;
        border-radius: 0px;
        font-size: 15px;
        font-family: $font-family;
        position: relative;
        left: 7%;
        &:hover{
            border-color: $btwColor1;
        }
        &::selection{
            border-color: $btwColor1;
        }
    }
    .lable{
        background-image: linear-gradient( to right, $btwColor1, $btwColor1);
        padding-top: 5px;
        font-size: $subsectionTitle-text;
        font-weight: 600;
        font-family: $font-title;

        width: 184px;
        height: 39px;
        color: $btwColor2;

        justify-content: center;
        text-align: center;
        margin-bottom: 15px;
    
    }
    .btn{
        background-image: linear-gradient( to right, $btwColor1, $btwColor1);
        border-radius: 5px;
        color: $btwColor2;
        width: 110px;
        height: 32px;
        position: relative;
        bottom: 0px;
        left: 85px;

        color: $btwColor2;
        font-size: $subsub-title-text;
        font-family: $font-title;
        font-weight: 500;

        &:hover{
            transform: scale(1.05);
            
        }

        
    
    }
}






