@import "../../../src/scss/abstracts/_variable.scss";

.movieeditcontainer{
    padding: 70px 0px 40px 100px;
    width: 100vw;
    min-height: $min-heigh-page;
    position: relative;
    &__breadcrumb {
        margin: 20px auto;
        text-align: center;
        display: inline-block;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
        overflow: hidden;
        border-radius: 5px;
        counter-reset: flag;
        width: auto;

        &__step {
            cursor: pointer;
            text-decoration: none;
            outline: none;
            display: block;
            float: left;
            font-size: 18px;
            font-weight: 500;
            font-family: $font-title;
            line-height: 36px;
            padding: 0 10px 0 60px;
            position: relative;
            background: #F4E5D7;
            color: $btwColor2;
            transition: background-color .5s;

            &:first-child {
                padding-left: 46px;
                border-radius: 5px 0 0 5px;

                &::before {
                    left: 14px;
                }

            }


            &:last-child {
                border-radius: 0 5px 5px 0;
                padding-right: 20px;

                &::after {
                    content: none;
                }


            }

            // circle
            &::before {
                content: counter(flag);
                counter-increment: flag;
                border-radius: 100%;
                width: 20px;
                height: 20px;
                line-height: 20px;
                margin: 8px 0;
                position: absolute;
                top: 0;
                left: 30px;
                font-weight: bold;
                background: #F4E5D7;
                box-shadow: 0 0 0 1px #F4E5D7;
            }

            // arrow
            &::after {
                content: '';
                position: absolute;
                top: 0;
                right: -18px;
                width: 36px;
                height: 36px;
                transform: scale(0.707) rotate(45deg);
                z-index: 1;
                border-radius: 0 5px 0 50px;
                background: #F4E5D7;
                transition: background-color 0.5s;
                box-shadow: 2px -2px 0 2px $btwColor4;

            }

            &--active {
                color: $btwColor2;
                background: $btwColor1;

                &::before {
                    color: $btwColor2;
                }

                &::after {
                    background: $btwColor1;
                }
            }


            &:hover {
                color: $btwColor2;
                background: #FEC13D;

                &::before {
                    color: $btwColor2;
                }

                &::after {
                    color: $btwColor2;
                    background: #FEC13D;
                }
            }


        }
    }
    &__content{
        width: 100%;
        &__cinemaandprovine{
            top: 0px;
            position: relative;
            display: flex;
            flex-wrap: wrap;
            z-index: 3;

            margin-bottom: 20px;
        }
        &__schedule{
            z-index: 2;
        }

    }
    &__btn {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: right;
        margin-top: 20px;
        padding-right: 140px;

        .btn {
            font-family: $font-title;
            font-size: 16px;
            font-weight: 100;
            text-transform: capitalize;

            border-radius: 10px;
            width: 130px;
            margin-right: 20px;
            padding: 5px 15px;
           

            &:hover {
                transform: scale(1.05);
            }

            &--contained {
                background-color: $btwColor1;
                color: $btwColor2;
                border: 1px solid $btwColor1;

                &:hover {
                    background-color: $btwColor1;
                }
            }

            &--outlined {
                background-color: $btwColor2;
                color: $btwColor1;
                border: 1px solid $btwColor1;

                &:hover {
                    background-color: $btwColor2;
                }
            }
        }
    }
}
.movieedit__btn{
        font-family: $font-title;
        font-size: $normal-text;
        font-weight: 500;
        text-transform: capitalize;

        border-radius: 10px;
        width: 130px;
        margin-right: 20px;
        padding: 5px 15px;
        background-color: $btwColor1;
        color: $btwColor2;
        border: 1px solid $btwColor1;

        &:hover {
            background-color: $btwColor1;
            transform: scale(1.03);
        }
    
}