@import "../../../../../scss/abstracts/_variable.scss";

.combocontainer{
    margin-bottom: 10px;
    width: 40vw;
    &__name{
        font-family: $font-family-normal;
        font-size: $itemTitle-text;
        font-weight: bold;
        color: $btwColor6;
        width: 100%;
        background-color: $btwColor2;
        margin-bottom: 10px;
        padding: 5px;
        padding-left: 20px;
    }
    &__description,
    &__priceandquantity__price
    {
        font-family: $font-family-normal;
        font-size: 17px;
        font-weight: normal;
        color: $btwColor5;
        margin-bottom: 5px;
        padding-left: 20px;
    }
    &__priceandquantity{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: space-between;
        padding-right: 20px;
    }
}