@import "/src/scss/abstracts/_variable.scss";

.footer {
    display: flex;
    flex-flow: row wrap;
    padding: 30px 30px 20px 30px;
    color: #2f2f2f;
    background-color: $btwColor2;

    margin-top: 30px;

    width: 100vw;
    &  > * {
        flex:  1 100%;
      }
    &__logo {
        margin-right: 1.25em;
        margin-bottom: 2em;

        width: 15vw;
        img{
            object-fit: cover;
        }
      }
      &__nav {
            display: flex;
            flex-flow: row wrap;
            flex-wrap: wrap;
          &> * {
            flex: 1 50%;
            margin-right: 1.25em;
          }
          &__item{ 
            .title {
                font-size: 20px;
                font-family: $font-title;
                font-weight: 500;
                color: $btwColor6;
                padding-bottom: 10px;
            } 
            .ul{
                list-style: none;
                padding-left: 0;
                padding-left: 10px;
                li {
                    line-height: 2em;
                    padding-bottom: 5px;
                    div{
                        color: $btwColor6;
                        font-family: $font-family-normal;
                        font-size: $normal-text;
                        font-weight: normal;
                    }
                    a{
                        color: $btwColor6;
                        font-family: $font-family-normal;
                        font-size: $normal-text;
                        font-weight: normal;

                        text-decoration: underline;

                        &:hover{
                            color: $btwColor1;
                        }
                    }
            
                  }
                  &--extra {
                    column-count: 1;
                    column-gap: 1.25em;
                  }
            }
            &--extra {
                column-count: 2;
                column-gap: 1.25em;
              }
          }

          
      }
 
      .legal {
        display: flex;
        flex-wrap: wrap;
        color: $btwColor6;
      }
      .heart {
        color: $btwColor7;
      }
      
      .legal__links {
        display: flex;
        align-items: center;
      }
       
      
  }

  @media screen and (min-width: 24.375em) {
    .legal .legal__links {
      margin-left: auto;
    }
  }
  
  @media screen and (min-width: 40.375em) {
    .footer__nav > * {
      flex: 1;
    }
    
    .footer__nav__item--extra {
      flex-grow: 1;
    }
    
    .footer__logo {
      flex: 1 0px;
    }
    
    .footer__nav {
      flex: 3 0px;
    }
  }