@import "/src/scss/abstracts/_variable.scss";

.ticketcontainer {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 40px 0;
    height: auto;
    min-height: 120px;

    &__nametimestate {
        width: 250px;
        line-height: 30px;
        font-family: $font-title;

        color: $btwColor6;
    

        .name {
            font-size: $sectionTitle-text;
            text-transform: capitalize;
            font-weight: normal;
        }

        .order .time {
            font-size: $normal-text;
        }

        .time {
            font-weight: 400;
        }

        .state {
            color: $btwColor5;
            font-size: $itemTitle-text;
            font-family: $font-title;
            font-weight: 500;

            &--active {
                color: $btwColor1;
            }
        }

    }

    &__cenimaseatroom {
        padding: 15px;
        flex: auto;
        display: flex;
        flex-wrap: wrap;
        background-color: $btwColor5;
        border-radius: 10px;
        vertical-align: middle;
        padding: 10px 20px;

        .column {
            flex: 1;
            margin-left: 20px;

            &__seattitle,
            &__roomtitle,
            &__timetitle {
                font-size: $itemTitle-text;
                text-align: center;
                font-family: $font-title;
                color: $btwColor2;
                font-weight: 400;
            }

            &__seats,
            &__room,
            &__time {
                font-size: 20px;
                text-align: center;
                font-family: $font-title;
                color: $btwColor2;
                font-weight: 600;
            }
        }

        .column__nameaddress {
            flex: 1.5;

            &__nametite {
                font-family: $font-title;
                font-size: $itemTitle-text;
                font-weight: bold;
                color: $btwColor2;
            }

            &__address {
                padding-top: 5px;
                font-family: $font-title;
                font-size: 14px;
                color: $btwColor2;
            }
        }

        .btndownload {
            color: $btwColor1;
            background-color: $btwColor5;
            border: 0px;
            border-radius: 50%;
            
            margin-left: 30px;
            flex: 0.2;
            
        }
    }

}