@import "../../scss/abstracts/_variable.scss";

.viewmore{
  position: absolute;
  top: 100%;
  padding-top: 40px;
  padding-right: 80px;
  right:0%;
  &__button{
    font-size: $normal-text;
    font-family: $font-family-normal;
    color: $btwColor1;
    font-style: italic;
    font-weight: 500;
    text-align: right;

    left: 0;
    flex: 1;

    background-color: transparent;
    width: auto;

    padding: 0px 10px 10px 0px;

&__icon{
    color: $btwColor1;
    width: 14px;
    margin: 10px 5px 0px 5px;
}
  }
  &:hover{
    transform: scale(1.05);
}
}
.title {
  font-family: $font-title;
  font-size: $sectionTitle-text;
  z-index: 1;
  padding-left: 10px;
}

.titlespan {
  height: 2px;
  width: 100px;
  margin-left: 10px;
  background-color: $btwColor1;
  position: absolute;
  z-index: 1;
  left: 0;
}

.homepage {
  width: 100vw;
  height: auto;
  min-height: $min-heigh-page;

  .banner {
    .swiper {
      width: 100%;
      height: 100%;
      color: $btwColor6;
      z-index: 1;

      .swiper-button-next {
        color: $btwColor3;
        height: 20px;
        width: 20px;
        padding-right: 20px;
        position: absolute;
      }

      .swiper-button-prev {
        color: $btwColor5;
        height: 20px;
        width: 20px;
        padding-left: 20px;
        position: absolute;
      }

      .swiper-pagination-bullet {
        background-color: $btwColor6;
      }

      .swiper-pagination-bullet-active {
        background-color: $btwColor1;
      }
    }

    .quickbooking {
      z-index: 1;
    }

  }

  .promotion {
    margin-left: 50px;
    padding-top: 20px;
    position: relative;
    &__title{
      margin-left: 30px;
      font-family: $font-title;
      font-size: $sectionTitle-text;
      z-index: 1;
    
    }
    &__titlespan{
      height: 2px;
      width: 80px;
      margin-left: 30px;
      background-color: $btwColor1;
      position: absolute;
      z-index: 1;
      left: 0;
    }
    .promotionList {
      margin-left: 20px;
      justify-content: center;
      align-items: center;
      margin-top: 15px;
    }
  }

  
}

.introduce {
  margin-left: 70px;
  margin-right: 70px;
  padding-bottom: 20px;
  text-align: justify;
  position: relative;

  .introdetail {
    margin-top: 15px;
    margin-left: 10px;

    font-size: $normal-text;
    line-height: 25px;
    font-family: $font-family-normal;
    color: $btwColor6;
    &__title{
      margin-left: 30px;
      font-family: $font-title;
      font-size: $sectionTitle-text;
      z-index: 1;
    
    }
    &__titlespan{
      height: 2px;
      width: 60px;
      margin-left: 30px;
      background-color: $btwColor1;
      position: absolute;
      z-index: 1;
      left: 0;
    }
  }
}