@import "../../../../scss/abstracts/_variable.scss";

.choiceseatcontainer {
    min-height: 100vh;
    &--hide{
        display: none;
    }
    &__seats {
            border: 2px solid $btwColor2;
            padding: 20px;
            margin-right: 50px;
            .screen {
                width: 100%;
                height: 10px;
                border: solid 2px;
                margin-bottom:10px ;
                background-color: $btwColor6;
                border-color: $btwColor6 transparent transparent transparent;
                border-radius: 50%/100px 100px 0 0;
                box-shadow: 0 50px 200px rgb(255, 255, 255);
                filter: drop-shadow(0px 10px 35px #ffffff);
            }
            .seatrows{
                margin-bottom: 10px;
            }
            .showcase{
                margin-top: 20px;
                background: rgba(255, 255, 255, 0.096);
                padding: 5px 10px;
                border-radius: 5px;
                color: $btwColor6;
                list-style-type: none;
                display: flex;
                justify-content: space-between;
                &__seats{
                    display: flex;
                    flex-wrap: wrap;
                    &__seat {
                        height: 20px;
                        width: 20px;
                        margin: 3px;
                        margin-right: 10px;
                        border-top-left-radius: 10px;
                        border-top-right-radius: 10px;
                        color: $btwColor5;
                        &--selected {
                            color: $btwColor1;
                          }                        
                        &--occupied {
                            color: #8E8E8E;
                        } 
                    }
                    p{
                        padding-top: 4px;
                    }
                }

            }
        }
}