@import "../../../scss/abstracts/_variable.scss";

.movieschedule__addshowtime__title{
    font-size: $sectionTitle-text;
    font-family: $font-title;
    font-weight: 100;
}
.movieschedule__addshowtime__name{
    font-size: $subsectionTitle-text;
    font-family: $font-title;
    font-weight: 100;

    margin-bottom: 10px;
}
.movieschedule__addshowtime__showtime{
    display: flex;
    flex-wrap: wrap;
    .lable{
        font-size: $subsub-title-text;
        font-family: $font-family-normal;
        font-weight: 500;
        margin-top: 6px;
    }
}
.movieschedule__addshowtime__btn{
    font-family: $font-title;
    font-size: $subsub-title-text;
    font-weight: 100;
    text-align: center;
    justify-content: center;
    text-transform: capitalize;

    width: 110px;

    border-radius: 10px;
    border: 0px;

    margin-right: 20px;
    padding: 5px 10px;

    &:hover {
        transform: scale(1.05);
    }

    &--contained {
        background-color: $btwColor1;
        color: $btwColor2;

        &:hover {
            background-color: $btwColor1;
        }
    }

    &--outlined {
        background-color: $btwColor2;
        color: $btwColor1;
        border: 1px solid $btwColor1;

        &:hover {
            background-color: $btwColor2;
        }
    }
}   