@import "/src/scss/abstracts/_variable.scss";

.bookingticketcontainer {
    padding: 80px 100px 40px 120px;
    height: auto;
    width: 100vw;
    justify-content: center;

    &__breadcrumb {
        margin: 20px auto;
        text-align: center;
        display: inline-block;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
        overflow: hidden;
        border-radius: 5px;
        counter-reset: flag;
        width: auto;

        &__step {
            text-decoration: none;
            outline: none;
            display: block;
            float: left;
            font-size: 18px;
            font-weight: 500;
            font-family: $font-title;
            line-height: 36px;
            
            padding: 0 10px 0 60px;
            position: relative;

            cursor: pointer;

            background: #F4E5D7;
            color: $btwColor2;
            transition: background-color .5s;

            &:first-child {
                padding-left: 46px;
                border-radius: 5px 0 0 5px;

                &::before {
                    left: 14px;
                }

            }


            &:last-child {
                border-radius: 0 5px 5px 0;
                padding-right: 20px;

                &::after {
                    content: none;
                }
            }
            // circle
            &::before {
                content: counter(flag);
                counter-increment: flag;
                border-radius: 100%;
                width: 20px;
                height: 20px;
                line-height: 20px;
                margin: 8px 0;
                position: absolute;
                top: 0;
                left: 30px;
                font-weight: bold;
                background: #F4E5D7;
                box-shadow: 0 0 0 1px #F4E5D7;
            }

            // arrow
            &::after {
                content: '';
                position: absolute;
                top: 0;
                right: -18px;
                width: 36px;
                height: 36px;
                transform: scale(0.707) rotate(45deg);
                z-index: 1;
                border-radius: 0 5px 0 50px;
                background: #F4E5D7;
                transition: background-color 0.5s;
                box-shadow: 2px -2px 0 2px $btwColor4;

            }

            &--active {
                color: $btwColor2;
                background: $btwColor1;

                &::before {
                    color: $btwColor2;
                }

                &::after {
                    background: $btwColor1;
                }
            }


            &:hover {
                color: $btwColor2;
                background: #FEC13D;

                &::before {
                    color: $btwColor2;
                }

                &::after {
                    color: $btwColor2;
                    background: #FEC13D;
                }
            }


        }
    }

    &__step {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: start;

        &__img {
            justify-content: center;
            margin-right: 50px;
            &--hide{
                display: none;
            }

            img {
                width: 16vw;
                object-fit: cover;
                aspect-ratio: 7/10;
            }

            p {
                align-items: center;
                text-align: center;
                width: 16vw;
                font-family: $font-family-normal;
                font-weight: 600;
                font-size: 30px;
                letter-spacing: 1px;
                text-transform: uppercase;
            }
        }

       
        &__ticket{
            justify-content: center;
            text-align: center;
            padding: 20px;
            background-color: #c4c4c425;
            height: fit-content;

            width: 23%;
            
            .title{
                font-family: $font-title;
                font-size: 25px;
                color: $btwColor1;
                text-transform: uppercase;
                margin-bottom: 10px;
            }
            .description{
                font-family: $font-family-normal;
                font-size: 15px;
                color: $btwColor5;
                margin-bottom: 5px;
            }
            .table{
                padding:10px 10px;
                background-color: transparent;
                filter: drop-shadow(0);
                .MuiTableRow-root{
                    background-color: $btwColor4;
                    color: $btwColor6;
                }
            }
            .foodscontainer{
                justify-content: left;
                text-align: left;
                border: 0px;
                height: fit-content;
                &__hover{
                    transform: scale(1);
                }
                &__title{
                    font-family: $font-family-normal;
                    font-size: $normal-text;
                    font-weight: bold;
                    color: $btwColor1;

                    margin-top: 15px;
                    margin-bottom: 5px;
                }
                &__row{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    &__food{
                        font-family: $font-family-normal;
                        font-size: 15px;
                        font-weight: 500;
                        color: $btwColor5;
                        margin-bottom: 5px;
                    }
                    &__total{
                        font-family: $font-family-normal;
                        font-size: 18px;
                        font-weight: bold;
                        color: $btwColor1;
                        margin-top: 10px;
                    }
                }
               
            }
            
            .btncontainer{
                justify-content: space-between;
                text-align: start;
                width: 100%;

                display: flex;
                flex-wrap: wrap;
                &__btn{
                    margin-top: 20px;
                    background: $btwColor1;
                    width: 100px;
                    height: 36px;
                    //margin-right: 10px;
                    padding: 3px 10px 5px 10px;
                    border-radius: 10px;
                    border: 1px solid $btwColor1;

                    font-size: 15px;
                    font-weight: 100;
                    color: $btwColor2;
                    font-family: $font-title;
                    &:hover{
                        transform: scale(1.05);
                        background: $btwColor1;
                    }
                    &--border{
                        border-color: $btwColor1;
                        background: $btwColor2;
                        color: $btwColor1;
                        margin-right: 0px;
                        &:hover{
                            background: $btwColor2;
                            border-color: $btwColor1;
                        }
                    }
                }
                
            }
            


        }
        
    }
}