@import "/src/scss/abstracts/_variable.scss";

.loginPage {
  width: 100%;
  height: 100%;
  display: flex;
 // max-width: 2000px;
  background-color: $btwColor2;

  &__leftcontainer{
    flex: 0.35;
    margin-top: 50px;
    text-align: center;
    justify-content: center;
  
    &__logo {
    margin-left: calc((100% - 160px)/2);

      width: 190px;
      height: 110px;
      text-align: center;
      justify-content: center;
      img{
        object-fit: cover;
      }
    }
    &__img{
      height: 300px;
    }
  }

  &__rightcontainer{
    flex: 0.65;
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow-y: auto;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    border-radius: 30px 0 0 30px;
    background-color: $btwColor4;
    padding: 10px 10px;
    overflow-x: hidden;
    &__tablist {
      width: fit-content;
      margin: auto;
      margin-bottom: 20px;
      color: white;

      .tab {
        width: 200px;
        font-size: 20px;
        font-family: $font-title;
        font-weight: 300;
      }

    }

    &__content {
      margin-top: 20px;
      overflow-y: hidden;
    }
  }
  &__closebtn{
    position: absolute;
    top: 20px;
    right: 20px;
  }
}
