@use 'scss/abstracts/index'as *;

@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Oswald&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600;800&family=Oswald&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 100%;
    scroll-behavior: smooth;

    @include tablet {
        font-size: 80%;
    }

    @include mobile {
        font-size: 60%;
    }
}

body {
    background-image: linear-gradient(to right, $btwColor4, $btwColor4);
    background-color: $btwColor4;
    line-height: 1, 5;
    color: $btwColor6;
    width: 100vw;
    height: auto;
    min-height: 100vh;
    justify-content: center;
    min-height: 100vh;
    min-width: 100vw;
    overflow-x: hidden;

    display: flex;
    background-repeat: no-repeat;
    background-size: cover;

    font-family: 'Montserrat', serif;

    @include mobile {
        margin-bottom: 3rem;
    }
}

a {
    text-decoration: none;
    color: unset;
}

@media (hover :hover) {
    a:hover {
        color: $btwColor1
    }
    ;
}

ul {
    list-style-type: none;
}

img {
    max-width: 100%;
}

iframe {
    border: none;
}

.container {
    width: 1660px;
    margin: auto;
}

.mb-3 {
    margin-bottom: 3rem;
}

.mb-2 {
    margin-bottom: 2rem;
}

.section {
    padding: 2rem;
}

.section__header {
    @include flex (center, space-between)
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}