@import "/src/scss/abstracts/_variable.scss";

.logintab {
  width: 70%;
  margin: auto;
  padding-top: 40px;
  justify-content: center;
  align-items: center ;
  text-align: center;

  &__password {
    margin-top: 10px;
    width: 100%;

    

    
  }

  
    .btn{
      margin-top: 30px;
      background: $btwColor1;
      width: 130px;
      height: 40px;
      border-radius: 7px;
      font-size: 16px;
      font-weight: 600;
      color: black;
      font-family: $font-title;
    
   align-self: center;

    &:hover {
      transform: scale(1.05);
      background: $btwColor1;
    }
  }
  &__subtitle{
    margin-top: 12px;
    font-size: 10px;
    font-family: $font-family;
    color: white;
    &__a{
      margin-left: 5px;
      font-weight: 800px;
      color: $btwColor1;
    }
  }
}
