@font-face{font-family:"Montserrats";src:url("../../styles/fonts/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Oswald";src:url("../../styles/fonts/Oswald-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrats_Normal";src:url("../../styles/fonts/Montserrat-VariableFont_wght.ttf") format("truetype")}.foodcontainer{border:1px solid #fff;height:auto;position:relative;aspect-ratio:7.5/10}.foodcontainer:hover{transform:scale(1.05)}.foodcontainer__img{position:relative;display:flex;flex-wrap:wrap;height:60%;min-height:200px;width:100%;object-fit:cover;aspect-ratio:1/1;z-index:1;box-shadow:0px 7px 10px rgba(0,0,0,.5);transition:.1s ease-in;align-items:flex-end;text-align:center;vertical-align:center;background-image:linear-gradient(to right, #F4E5D7, #F4E5D7)}.foodcontainer__img .btn{font-family:"Oswald";font-size:15px;font-weight:100;text-align:center;justify-content:center;text-transform:capitalize;width:120px;border-color:#feae007a;opacity:0;z-index:3;position:absolute;top:calc(50% - 10px);left:calc(50% - 30px)}.foodcontainer__img .btn--hide{display:none}.foodcontainer__img .btn::before{content:"Chọn ảnh";display:inline-block;border:1px solid #999;border-radius:3px;padding:5px 8px;outline:none;-webkit-user-select:none;cursor:pointer;text-shadow:1px 1px #fff;font-weight:700;font-size:10pt}.foodcontainer__img .btn::-webkit-file-upload-button{visibility:hidden}.foodcontainer__img::before{position:absolute;top:0;left:0;display:block;width:100%;height:100%;transform:scale(1.05);background:rgba(0,0,0,.6);z-index:2;transition:.5s;opacity:1}.foodcontainer__img:hover::before{opacity:1}.foodcontainer__img:hover .btn{opacity:1;transform:scale(1.05)}.foodcontainer__img:hover img{transform:scale(1.05)}.foodcontainer__infor{display:flex;flex-direction:column;padding:10px;height:40%}.foodcontainer__infor__name{padding-top:5px;height:fit-content;font-family:"Montserrats";font-size:18px;font-weight:600;color:#feae00;flex:1;height:40px;display:-webkit-box;-webkit-line-clamp:2;line-clamp:2;text-overflow:ellipsis;-webkit-box-orient:vertical;margin-bottom:5px}.foodcontainer__infor__description{font-family:"Montserrats_Normal";font-size:13px;flex:auto;height:fit-content;overflow:hidden;text-overflow:ellipsis;color:#fff;display:-webkit-box;-webkit-line-clamp:2;line-clamp:2;text-overflow:ellipsis;-webkit-box-orient:vertical}.foodcontainer__infor__price{font-family:"Montserrats";font-size:18px;font-weight:500;color:#fff;flex:1;text-overflow:ellipsis;margin-bottom:5px}.foodcontainer__icon{width:35px;height:35px;border-radius:50%;justify-content:center;text-align:center;position:absolute;top:1%;right:1%;z-index:4}.foodcontainer__icon:hover{transform:scale(1.05)}