@import "/src/scss/abstracts/_variable.scss";

.qacontainer{
    width: 100%;
    justify-content: center;
    align-items: center;
    align-content: center;
    &__question{
        width: 100%;
        background-color: #00000030;
        border-radius: 0 10px 0 10px;
        font-size: 22px;
        font-weight: 500;
        font-family: $font-family;
        padding: 10px;
        &--active{
            width: 80vw;
            background-color: $btwColor1;
            color: $btwColor2;
            border-radius: 0 10px 0 10px;
            font-size: 22px;
            font-weight: bold;
            font-family: $font-family;
           padding: 10px;
        }
    }
    &__answer{
        white-space: pre-line;
        width: 100%;
        background-color: #00000030;
        font-size: 22px;
        font-family: $font-family;
        line-height: 20px;
        padding: 10px;
        margin-bottom: 10px;
    }
}
