@import "../../../../scss/abstracts/_variable.scss";

.confirmcontainerparent {
    .confirmcontainer {
        border: solid #000;
        border-width: 2px;
        width: 56vw;
        margin-right: 50px;

        &--hide {
            display: none;
        }

        &__title {
            font-family: $font-title;
            font-size: 24px;
            font-weight: 500;
            color: $btwColor1;
            width: 100%;
            background-color: $btwColor2;
            padding-left: 20px;
            padding-top: 5px;
            padding-bottom: 7px;
            margin-bottom: 15px;
        }

        &__detail {
            margin: 4px 20px;
            font-family: $font-family;
            font-size: 20px;
            color: $btwColor5;
            font-weight: 300;
            margin-left: 20px;

            &--small {
                font-size: 18px;
                margin-left: 20px;
            }

        }

        Button {
            margin-left: 20px;
            font-weight: bold;
            color: $btwColor2;
            background-color: $btwColor1;
            margin: 16px;
            border-radius: 10px;
            text-transform: capitalize;

            &:hover {
                transform: scale(1.05);
                background-color: $btwColor1;
                border-color: $btwColor1;
            }
        }

        &__button {
            Button {
                margin-left: 20px;
                margin: 16px;
                background-color: $btwColor2;
                color: $btwColor1;
                text-transform: capitalize;
                border: solid $btwColor1;
                border-width: 1px;

                &:hover {
                    transform: scale(1.05);
                    background-color: $btwColor2;
                    border-color: $btwColor1;

                }
            }
        }

        &__hr {
            width: 50%;
            border: 1px solid $btwColor1;
            margin: 10px 20px;
        }

        ul {
            list-style: circle;
            margin-left: 30px;
        }
    }

}