@font-face{font-family:"Montserrats";src:url("../../styles/fonts/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Oswald";src:url("../../styles/fonts/Oswald-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrats_Normal";src:url("../../styles/fonts/Montserrat-VariableFont_wght.ttf") format("truetype")}.foodpagecontainer{padding:80px 0px 40px 120px;width:calc(100vw - 120px);overflow:hidden;min-height:100vh}.foodpagecontainer__titlebtn{justify-content:space-between;display:flex;flex-wrap:wrap}.foodpagecontainer__titlebtn__title{font-size:35px;font-family:"Oswald";font-weight:500;margin-bottom:10px}.foodpagecontainer__titlebtn__btn{margin-right:5px;padding:6px 10px 5px 10px;height:35px;background-color:#feae00;color:#0d0e13;font-size:15px;font-family:"Oswald";font-weight:100;text-align:center;border-radius:5px;border-color:#feae00;cursor:pointer}.foodpagecontainer__titlebtn__btn:hover{transform:scale(1.05);background-color:#feae00}.foodpagecontainer__foodgrid{margin-left:auto;margin-right:auto;padding:10px;display:grid;grid-gap:30px;grid-template-columns:repeat(auto-fit, minmax(calc((100% - 150px) / 4), 0));max-width:2000px}.foodpagecontainer__titlemovie{font-family:"Oswald";font-size:25px;z-index:1;margin-top:15px}.foodpagecontainer__titlespanmovie{height:3px;width:170px;margin-left:120px;margin-bottom:20px;background-color:#feae00;position:absolute;z-index:1;left:0}.foodpagecontainer__movieshowing{height:fit-content;margin-top:15px}.foodpagecontainer__movieshowing .swiper{padding-top:15px;padding-bottom:15px;height:auto}.foodpagecontainer__movieshowing .swiper__slide{height:100%;width:fit-content}.addfoodcontainer__title{font-family:"Oswald";font-size:25px;font-weight:500;color:#fff;margin-bottom:20px}.addfoodcontainer__imgbtnfield{display:flex;flex-wrap:wrap}.addfoodcontainer__imgbtnfield__imgbtn{position:relative;width:25%;margin-right:20px;justify-content:center;align-items:center;text-align:center;z-index:1}.addfoodcontainer__imgbtnfield__imgbtn::before{content:"";position:absolute;top:0;left:0;display:block;width:100%;height:100%;transform:scale(1.05);background:rgba(0,0,0,.6);z-index:2;transition:.5s;opacity:0}.addfoodcontainer__imgbtnfield__imgbtn img{aspect-ratio:2/3;width:100%;height:100%;object-fit:cover}.addfoodcontainer__imgbtnfield__imgbtn input{text-align:center;justify-content:center;width:120px;opacity:0;z-index:3;position:absolute;top:calc(50% - 10px);left:calc(50% - 35px)}.addfoodcontainer__imgbtnfield__imgbtn input::before{content:"Chọn ảnh";display:inline-block;border-radius:5px;padding:8px 10px;outline:none;-webkit-user-select:none;cursor:pointer;font-family:"Oswald";font-weight:700;font-size:10pt;background-color:#feae00;color:#0d0e13}.addfoodcontainer__imgbtnfield__imgbtn input::-webkit-file-upload-button{visibility:hidden}.addfoodcontainer__imgbtnfield__imgbtn:hover::before{opacity:1}.addfoodcontainer__imgbtnfield__imgbtn:hover input{opacity:1;transform:scale(1.05)}.addfoodcontainer__imgbtnfield__imgbtn:hover img{transform:scale(1.05)}.addfoodcontainer__imgbtnfield__field{width:65%}.addfoodcontainer__btn{font-family:"Oswald";font-size:15px;font-weight:100;text-align:center;justify-content:center;text-transform:capitalize;width:110px;border-radius:10px;border:0px;margin-right:20px;padding:5px 10px}.addfoodcontainer__btn:hover{transform:scale(1.05)}.addfoodcontainer__btn--contained{background-color:#feae00;color:#0d0e13}.addfoodcontainer__btn--contained:hover{background-color:#feae00}.addfoodcontainer__btn--outlined{background-color:#0d0e13;color:#feae00;border:1px solid #feae00}.addfoodcontainer__btn--outlined:hover{background-color:#0d0e13}.addfoodcontainer__content{font-family:"Montserrats";font-size:18px;color:#fff}