@import "../../../scss/abstracts/_variable.scss";

.commentcontainer{
    background-color: $btw25Color6;
    margin: 10px;
    padding: 20px;
    margin: 20px 0px;
    .commentcontainer__nameandstar{
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 10px;
        justify-content: space-between;

        .commentcontainer__nameandstar__name{
            font-family: $font-family-normal;
            font-size: $subTitle-text;
            font-weight: 600;
            color: $btwColor6;
        } 
        .commentcontainer__nameandstar__star{
            margin-right: 15px;
            .MuiRating-iconEmpty{
                color: $btwColor1;
              }
        }
    }
    .commentcontainer__comment{
        font-family: $font-family-normal;
        font-size: $normal-text;
        font-weight: normal;
        color: $btwColor6;
        line-height:25px;
        text-align: justify;
        padding-right: 20px;
        padding-top: 10px;
    }
}

