@import '../../scss/abstracts/_variable.scss';

.homeLink {
    display: flex;
    align-items: center;

    .logo-icon {
        margin-right: 10px;
        width: 30px;
        height: 30px;
        color: $btwColor1;
    }

    .logo-name {
        font-family: $font-title;
        font-size: 24px;

    }
}


.header {
    height: 50px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #00000040;
    width: 100vw;
    z-index: 99;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    transition: height 0.3s ease, background-color 0.3s ease;

    &.shrink {
        height: 60px;
        background-color: $btwColor2;

    }

    &__wrap {
        height: 100%;
        display: flex;
        align-items: center;
        &__logo{
            height: 50px;
            width: 80px;
            img{

                object-fit: cover;
            }
        }
       &__searchbar{
           display: flex;
           flex-wrap: wrap;
           height: 35px;
           width: 300px;
           background-image: linear-gradient(to right, $btwColor2, $btwColor2);
           border-radius: 10px;
           border: 1px solid $btwColor1;
           margin-left: 10px;
           vertical-align: center;
           .inputcontainer{
            height: calc(100% - 2px);
            width: calc(100% - 57px);
            padding: 3px 5px;

            Input{
                height: 100%;
                width: 100%;
 
                padding: 5px;
                margin-left: 10px;
                margin-right: 2px;
 
                background-color: $btwColor2;
                color: $btwColor6;
                border: 0px solid $btwColor2;
                border-radius:  10px;

 
                &:focus{
                 outline: none;
                }
            }
           }
          
           .iconbutton{
               width: 33px;
               height: 33px;

               border: 1px solid $btwColor1;
               border-radius: 8px 5px 5px 8px ;
               
               background-color: $btwColor1;
               &__icon{
                background-color: $btwColor1;
                color: $btwColor2;

                margin-left: 2px;
                padding: 3px;
               }
           }
       }
    }

    &__loginbutton {
        height: fit-content;
        background-color: $btwColor1;
        color: black;
        font-family: $font-title;
        font-size: 15px;
        border: 0px;
        border-radius: 5px;
        align-items: center;
        padding: 5px 15px 7px 15px;
        margin-top: 7px;
    }

}


.header__right {
    padding-left: 40px;
    display: flex;

    .header__nav {
        padding-right: 30px;
        display: flex;
        font-family: $font-title;
        font-weight: 500px;

        &>*~* {
            margin-left: 2rem;
        }

        li {
            padding: 10px 0;
            font-weight: 500;
            position: relative;
            font-size: 20px;
            letter-spacing: 1px;

            &::after {
                background-color: $btwColor1;

            }

            &.active::after,
            &:hover::after {
                width: 100%;
                color: $btwColor1;
            }
        }

    }

    .header__user {

        display: flex;
        align-items: center;
        padding-left: 40px;
        transition: 0.5s;
        padding-top: 10px;

        .avatar {
            vertical-align: middle;
            z-index: 4;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            object-fit: cover;
        }

        .username {
            width: 100px;
            padding-left: 10px;
            font-size: 13px;
            font-family: $font-family-normal;
            font-weight: 600;
            color: $btwColor6;
            z-index: 100;
            background-color: transparent;

            &:hover {
                color: $btwColor1;
            }
        }
    }
}