@import "../../../scss/abstracts/_variable.scss";


.rowLanding{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
    width: auto;
    cursor: default;
    p{
        font-size: 15px;
        font-weight: 200;
        font-family: $font-family;
        margin-right: 10px;
        cursor: default;
    }
    .icon{
        color: $btwColor1;
        width: 18px;
        height: 18px;
        margin-right: 5px;
    }
    .p-border{
        border: 1px solid $btwColor1;
        box-sizing: border-box;
        cursor: default;
    }
    .btn{
        margin-top: 20px;
        padding: 5px 10px;

        background: $btwColor1;
        width: 130px;
        height: 36px;
        border-radius: 10px;

        font-size: $normal-text;
        font-weight: 100 ;
        color: $btwColor2;
        font-family: $font-title;

        position: absolute;
        bottom: -20%;
        left: 0%;
        &:hover{
            transform: scale(1.05);
            background: $btwColor1;
        }
    }
    .btn-border{
        margin-top: 20px;
        padding: 5px 10px;
        width: 130px;
        height: 36px;
        border-color: $btwColor1;
        background: $btwColor2;

        color: $btwColor1;
        font-weight: 100 ;
        border-radius: 10px;
        font-size: $normal-text;
        font-family: $font-title;
        
        position: absolute;
        bottom: -20%;
        left: 25%;
        
        &:hover{
            transform: scale(1.05);
            background: $btwColor2;
            border-color: $btwColor1;
        }
    }
    .description{
        font-size: $normal-text;
        font-family: $font-family-normal;
        text-align: justify;
        line-height: 18px;
        cursor: default;
    }
    .description-bold{
        font-size: $normal-text;
        font-family: $font-family-normal;
        font-weight: bold;
        text-align: justify;
        cursor: default;
     
    }
}

.containerall{
    width: 100vw;
    height: 100vh;
    top: 0;
    position: relative;
    display: flex;
    flex: 1;
    .banner{
        width: 100%;
        height: 100%;
        position: absolute;
        overflow: hidden;
        img{
            position:absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
    &__des{
        position: absolute;
        top: 20%;
        left: 10%;
        &__name{
            font-size: 70px;
            font-family: $font-title;
            font-weight: bold;
            word-spacing: 1px;
            line-height: 80px;
            
            width: 70%;
            text-align: left;
            margin-bottom: 10px;

            cursor: default;
        }
        &__cateyeartime{
            height: 30px;
            display: flex;
            flex-wrap: wrap;
            .cate,
            .year,
            .time {
                font-size: $normal-text;
                font-weight: 200;
                font-family: $font-family;
                margin-right: 10px;

                cursor: default;
            }
            .icon{
                color: $btwColor1;
                width: 18px;
                height: 18px;
                margin-right: 5px;
            }
        }
        &__scoresub{
            display: flex;
            flex-wrap: wrap;
            .score,
            .sub{
                font-size: $normal-text;
                font-weight: 200;
                font-family: $font-family;
                margin-right: 10px;
                cursor: default;
            }
            .sub{
                border: 1px solid $btwColor1;
                box-sizing: border-box;
            }
            .icon{
                color: $btwColor1;
                width: 18px;
                height: 18px;
                margin-right: 5px;
            }
        }
        &__btn{
            display: flex;
            flex-wrap: wrap;
            .btn{
                margin-top: 20px;
                margin-right: 20px;
                padding: 5px 10px;
        
                background: $btwColor1;
                width: 130px;
                height: 36px;
                border-radius: 10px;
        
                font-size: $normal-text;
                font-weight: 100 ;
                color: $btwColor2;
                font-family: $font-title;
        
                &:hover{
                    transform: scale(1.05);
                    background: $btwColor1;
                }
            }
            .btn--border{
                border-color: $btwColor1;
                background: $btwColor2;
        
                color: $btwColor1;
                
                &:hover{
                    background: $btwColor2;
                    border-color: $btwColor1;
                }
            }
        }
       
    }
 
    .containerinfor{
        position: absolute;
        top: 60%;
        right: 3%;
        width: 35%;
    }
}