@import "/src/scss/abstracts/_variable.scss";

.textarea_ask {
    width: 100%;
    padding: 10px;
    font-family: $font-family-normal;
    font-size: 13px;
    background: white;
    border: none;
    text-align: start;
    border-radius: 5px;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

.askboxcontainer {
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;

    &__title {
        font-family: $font-title;
        font-size:$viewTitle-text;
        font-weight: 500;
        color: $btwColor6;
        margin-bottom: 10px;
    }

    &__contact {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: center;
        margin-bottom: 16px;
        vertical-align: bottom;
        font-weight: 300;

        &__emailphone {
            display: flex;
            flex-wrap: wrap;

            .icon {
                height: 16px;
                width: 16px;
                color: $btwColor1;
                margin-right: 5px;
                padding-top: 7px;
            }

            .emailphone {
                font-family: $font-title;
                font-size: 16px;
                font-weight: 500;
                color: $btwColor6;
                margin-right: 50px;
            }
        }
    }

    &__infor {
        background-color: #252425;
        border-radius: 10px;
        padding: 30px;
        margin: 0px 50px 0px 50px;
        width: calc(100% - 100px);
        grid-gap: 20px;
        justify-content: center;

        &__nameemailphone {
            display: grid;
            grid-template-columns: auto auto auto;
            padding: 10px;
            grid-gap: 20px;
            margin-bottom: 15px;

            .textfieldaskbox {
                &__title {
                    font-size: 15px;
                    font-family: $font-family;
                    font-weight: 500;
                    color: $btwColor6;
                    margin-bottom: 7px;
                    text-align: left;
                }

                &__field {
                    width: 100%;
                    margin-right: 20px;
                    font-family: $font-family-normal;
                }
            }

        }

        &__title {
            text-align: left;
            font-size: 15px;
            font-family: $font-family;
            font-weight: 500;
            color: $btwColor6;
            margin-bottom: 7px;
            margin: 7px 10px;
        }

        &__content {
            width: 100%;
            margin: 0px 10px;
            padding-right: 20px;

        }

        &__btn {
            justify-content: center;
            text-align: center;
            position: relative;
            .btn {
                background-color: $btwColor1;
                border-radius: 10px;
                font-family: $font-title;
                font-size: 20px;
                font-weight: 500;
                color: $btwColor2;
                width: 200px;
                margin-top: 40px;
                padding: 5px;

                text-align: center;

                position: absolute;
                left: calc(50% - 100px);
                

                &:hover {
                    transform: scale(1.05);
                    background-color: $btwColor1;
                }
            }

        }
    }
}