@import "../../scss/abstracts/_variable.scss";

.PromotionDetail {
    margin: auto;
    width: calc(100vw - 180px);
    max-width: 2000px;
    padding-top: 80px;
    padding-bottom: 40px;

    &__img {
        width: 60%;
        align-items: flex-start;
        padding-top: 20px;
    }

    &__title {
        font-family: $font-title;
        font-size: 30px;
    }

    &__content {
        padding-top: 20px;
        font-family: $font-family-normal;
        font-size: 14px;
        line-height: 25px;
        text-align: justify;

    }

    &__infor {
        line-height: 25px;

        &__title {
            font-weight: 600;
            font-size: 18px;
            color: $btwColor1;
            padding-top: 30px;
            padding-bottom: 10px;
        }

        &__text {
            font-family: $font-family-normal;
            font-size: 14px;
        }
    }

}

.PromotionDetail__suggest {
    padding-top: 20px;
    padding-bottom: 40px;
    padding-left: 0;
    align-items: flex-start;
    text-align: left;
    &__title {
        font-size: 20px;
        color: $btwColor1;
        display: flex;
        font-weight: 600;
        padding-left: 90px;
        padding-right: 90px;

        &__icon {
            size: 20px;
            padding-right: 20px;
        }
    }

    &__list {
        margin-left: 65px;
    }
}