@import "../../../src/scss/abstracts/_variable.scss";

.searchitemcontainer{
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    cursor: pointer;

    &:hover{
        transform: scale(1.03);
    }
    &__img{
        aspect-ratio: 3/4;
        object-fit: cover;
        width: 100%;
        height: 70%;
        border-radius: 10px;

        display: flex;
        flex-wrap: wrap;

        margin-right: 15px;
        margin-bottom: 10px; 
    }
    &__infor{
        &__name{
            font-family: $font-family;
            font-size: 15px;
            font-weight: 200;
            color: $btwColor6;
            margin-bottom: 5px;

            max-lines: 2;
            overflow-y: hidden;

        }
        &__rating{

            margin-bottom: 10px;

            .MuiRating-iconFilled{
                color: $btwColor1;
                width: 5px;
                height: 5px;
            }
            .MuiRating-iconHover{
                color: $btwColor1;
                font-size:large;
            }
            .MuiRating-iconEmpty{
                color: $btwColor1;
                font-size:large;
              }
        }
        &__age{
            color: $btwColor1;
            font-size: 13px;
        }

    }
}