@import "/src/scss/abstracts/_variable.scss";

.searchpagecontainer{
    width: 100vw;
    padding: 70px 120px 40px 100px;
    justify-content: start;
    text-align: start;
    min-height: $min-heigh-page;
    &__title{
        width: 100%;
        font-size: $sectionTitle-text;
        font-family: $font-title;
        font-weight: 500;
        color: $btwColor6;

        margin-bottom: 15px;
    }
    &__results{
        display: grid;
        grid-template-columns:  repeat(auto-fit, minmax(calc((100% - 150px)/ $resultsearch-in-row), 0));
        gap: 15px;
    }
}
