@import "/src/scss/abstracts/_variable.scss";

.password__title {
        text-align: start;
        font-size: 15px;
        font-family: $font-family;
        font-weight: 500;
        color: $btwColor6;
        margin-bottom: 7px;
        margin-top: 10px;
    }
.password__textfield {
        font-size: 13px;
        font-family: $font-family;
        padding: 10px;
        background-color: white;
        border: 0px;
        height: 40px;
        border: none;
        width: 100%;
        width: 100%;
        border-radius: 5px;

        input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
            -webkit-box-shadow: 0 0 0 30px white inset !important;
        }
        input::-ms-reveal {
            display:none;
          }

        ::placeholder {
            color: gray;
        }

    }
