@import "/src/scss/abstracts/_variable.scss";

.movielistcommentcontainer {
    margin-top: 30px;
    width: 100%;
    &__allcomment,
    &__titlecomment {
        font-family: $font-family;
        font-size: $subTitle-text;
        font-weight: 500;
        word-spacing: 1px;

        margin-bottom: 5px;
    }
    &__ratingcomment{
        width: 100%;
        text-align: left;
        &__rating{
            height: 30px;
            .MuiRating-iconFilled{
                color: $btwColor1;
            }
            .MuiRating-iconHover{
                color: $btwColor1;
            }
            .MuiRating-iconEmpty{
                color: $btwColor1;
              }
        }
      
    }
    &__usercomment{
        background-color: #ffffff30;
        border-radius: 10px;
        &:hover{
            border-color: #fff;

        }
        &__rate{
            height: 30px;
        margin-right: 15px;

        .MuiRating-iconEmpty {
            color: $btwColor1;
        }
        }
    }

    &__rate {
        height: 30px;
        margin-right: 15px;

        .MuiRating-iconEmpty {
            color: $btwColor1;
        }
    }

    &__btn {
        width: 100%;
        justify-content: right;
        text-align: right;
        padding-right: 10px;

        Button {
            font-family: $font-family;
            font-size: $subsub-title-text;
            font-weight: bold;
            text-align: center;
            justify-content: center;
            text-transform: capitalize;
            color: $btwColor2;

            width: 110px;

            border-radius: 5px;
            margin: 15px 0px 20px 0px;

            background-color: $btwColor1;

            &:hover {
                transform: scale(1.05);

                background-color: $btwColor1;
            }
        }
    }


}