@import "/src/scss/abstracts/_variable.scss";

.supportcontainer{
    z-index: 2;
    padding-top: 30px;
    min-height: $min-heigh-page;
    padding: 70px 100px 40px 100px;
    min-width: 100vw;
    justify-content: center;
    min-height: $min-heigh-page;


    &__title{
        font-size: $viewTitle-text;
        font-family: $font-title;
        font-weight: 500;
        margin-bottom: 10px;
    }
    
    &__tablist{
        margin-bottom: 20px;
        .tab{
          font-size: 20px;
          font-family: 'Oswald';
          font-weight: 300;
        }
          
    }
    &__content{
        width: 100%;
    }

}