@import "../../scss/abstracts/_variable.scss";

.movie {
    width: 100%;
    aspect-ratio: 7 / 10;
    flex-wrap: wrap;
    box-shadow: 0px 7px 10px rgba(black, 0.5);
    transition: 0.1s ease-in;
    position: relative;
    display: flex;
    align-items: flex-end;
    text-align: center;
    vertical-align: center;
    overflow: hidden;

    &__soldout {
      
            display: none;
    }

    &__x {
       // background-color: #4c4c4ccf;

        position: absolute;
        top: 2%;
        right: 2%;
        z-index: 4;

        justify-content: center;

        border-radius: 50%;

        &--hide {
            display: none;
        }
    }

    &:hover {
        &::before {
            opacity: 1;
        }

        .info {
            opacity: 1;
            transform: translateY(0px);
        }

        img {
            transform: scale(1.05);
        }

        .movie__x {
            transform: scale(1.05);
        }
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        transform: scale(1.05);
        background: rgba(black, 0.6);
        z-index: 2;
        transition: 0.5s;
        opacity: 0;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    h1 {
        font-family: $font-title;
        font-size: $sectionTitle-text;
        word-spacing: 1px;
        text-transform: uppercase;
    }

    p {
        letter-spacing: 1px;
        font-size: $normal-text;
        margin-top: 8px;
    }

    .info {
        opacity: 0;
        z-index: 3;
        position: absolute;
        top: calc(50% - 160px);
        text-align: center;
        padding: 0px 15px;
        justify-content: center;
        width: 100%;
        height: 100%;

        &__name {
            font-family: $font-title;
            font-size: $sectionTitle-text;
            word-spacing: 1px;
            font-size: $subsectionTitle-text;
        }

        &__age {
            background-image: linear-gradient(to right, $btwColor1, $btwColor1);
            border-radius: 50%;
            width: 45px;
            height: 45px;
            text-align: center;
            align-items: center;
            display: inline-block;
            left: 90%;

            &__value {
                font-family: $font-family;
                font-size: $normal-text;
                color: $btwColor2;
                font-weight: bold;
                text-align: center;
                display: inline-block;
                top: 50%;
                margin: 0;
                position: absolute;
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);
                position: relative;
            }
        }

        &__type {
            font-size: $normal-text;
            font-family: $font-family;
            font-weight: 400
        }

        &__onair {
            font-size: $normal-text;
            font-family: $font-family;
            font-weight: bold
        }

        Link {
            .info__link__btn {
                background-image: linear-gradient(to right, $btwColor1, $btwColor1);
                border-radius: 30px;
                color: $btwColor2;

                font-family: $font-title;
                font-size: $normal-text;
                font-weight: 100;

                &:hover {
                    color: $btwColor2;
                    transform: scale(1.05);
                }
            }
        }
    }

    &--stopshow {
        width: 100%;
        aspect-ratio: 7 / 10;
        flex-wrap: wrap;
        box-shadow: 0px 7px 10px rgba(black, 0.5);
        transition: 0.1s ease-in;
        position: relative;
        display: flex;
        align-items: flex-end;
        text-align: center;
        vertical-align: center;
        overflow: hidden;
    
        .movie__soldout {
            font-size: $itemTitle-text;
            font-family: $font-title;
            letter-spacing: 1px;
            color: $btwColor6;
            text-align: center;
            text-decoration: none;
            text-shadow: 0 0 0.75em #444;
    
            position: absolute;
            top: 1.5em;
            left: -4em;
           
            display: block;
            
            background-color: $btwColor7;
            padding: 0.3em 5em 0.3em 4.5em;
            
            -webkit-text-shadow: 0 0 0.75em #444;
            -webkit-box-shadow: 0 0 0.5em rgba(0, 0, 0, 0.5);
            -webkit-transform: rotate(-45deg) scale(1, 1);
    
            z-index: 5;
        }
    
        &__x {
            background-color: #4c4c4ccf;
    
            position: absolute;
            top: 2%;
            right: 2%;
            z-index: 4;
    
            justify-content: center;
    
            border-radius: 50%;
    
            &--hide {
                display: none;
            }
        }
    
        &:hover {
            &::before {
                opacity: 1;
            }
    
            img {
                transform: scale(1.05);
            }    
            .movie__x {
                transform: scale(1.05);
            }
        }
    
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            transform: scale(1.05);
            background: rgba(black, 0.6);
            z-index: 2;
            transition: 0.5s;
            opacity: 1;
        }
    
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    
        h1 {
            font-family: $font-title;
            font-size: $sectionTitle-text;
            word-spacing: 1px;
            text-transform: uppercase;
        }
    
        p {
            letter-spacing: 1px;
            font-size: $normal-text;
            margin-top: 8px;
        }
    
        .info {
            display: none;
        }
    
 
    }

}


h3 {
    font-size: $itemTitle-text; 
    font-family: $font-title;
    margin-top: 10px;
    margin: 10px;
    padding-top: 5px;

    font-weight: 100;
    text-align: center;
    text-transform: uppercase;
    word-spacing: 1px;
}