@import "/src/scss/abstracts/_variable.scss";

.tabs{
    z-index: 2;
    padding-top: 30px;
    padding-left: 70px;
    
    .tablist{
        .tab{
          font-size: 20px;
          font-family: $font-title;
          font-weight: 300;
        }
          
    }
; 
    .moviesgrid{
        justify-content: start;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(calc((100vw - 250px)/ $movie-in-row), 0));

        grid-gap: 30px;
        margin-top: 20px;

        min-height: calc((100vw - 250px)/ $movie-in-row * 10/7 + 30px);
        .emptyfilm{
            width: 100vw;

            justify-content: center;
            text-align: center;
            align-content: center;

            display: block;
            position: relative;
            &__content{
                position: absolute;
    
                top: calc(50% - 100px);
                left: calc(50% - 200px);
                &__icon{
                    
                    color: $btwColor1;
                    margin-bottom: 10px;
                }
                &__content{
                    font-family: $font-family;
                    font-size:$itemTitle-text ;
                    
                }
            }
           
        }
    }
    
  }
    
